import React from "react"
import { graphql, useStaticQuery, Link } from 'gatsby'
import styled from 'styled-components'
import { color, padding, margin, borderRadius, textSize, boxShadow, breakpoint } from '../../util/style_property'
import Img from "gatsby-image"

const Logo = () => {
  const data = useStaticQuery(
    graphql`
      query{
        file(name: {eq: "logo"}){
          childImageSharp{
            fluid {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
      }
    `
  )

  const imageData = data.file
  const LogoWrapper = styled(Link)`
    position: absolute;
    top: 0;
    z-index: 100;
    width: 96px;
    height: 96px;
    padding: 8px;
    background-color: ${color.white};
    border-radius: 0 0 32px 0;
    transition: background-color 0.2s;

    &:hover{
      background-color: #eee;
    }

    box-shadow: ${boxShadow.l};
    /* @media screen and (max-width: ${breakpoint.mobile_l}) {
      box-shadow: ${boxShadow.l}
    } */
  `
  return (
    <LogoWrapper to="/">
      <Img fluid={imageData.childImageSharp.fluid} />
    </LogoWrapper>
  )
}

export default Logo