import React from "react"
import { Link } from 'gatsby'
import styled from 'styled-components'
import { color, padding, margin, borderRadius, textSize, boxShadow, breakpoint } from '../../util/style_property'
import Logo from "../partial/logo"
import GlobalNavigation from "./global_navigation"

const GlobalHeader = () => {
  return (
    <Wrapper>
      <Inner>
        <Logo />
        <GlobalNavigation />
      </Inner>
    </Wrapper>
  )
}

const Wrapper = styled.div`
  width: 100%;
  height: 0;
  /* height: 64px; */
  /* @media screen and (max-width: ${breakpoint.mobile_l}) {
    height: 0;
  } */
`

const Inner = styled.header`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
  display:flex;
  width: 100%;
  /* とりあえず今はナビゲーションがないのでアイコンだけ */
  nav {
    display: none;
  }
  /* @media screen and (max-width: ${breakpoint.mobile_l}) {
    nav {
      display: none;
    }
  } */
`


export default GlobalHeader
