import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import styled from 'styled-components'
import { color, padding, margin, borderRadius, textSize, boxShadow, breakpoint } from '../../util/style_property'
import GlobalNavigation from "./global_navigation"

const GlobalFooter = () => {
  const { site } = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            title
            author
          }
        }
      }
    `
  )
  return (
    <Wrapper>
      <Copyright>&copy; 2020 {site.siteMetadata.author} - {site.siteMetadata.title}</Copyright>
    </Wrapper>
  )
}

const Copyright = styled.small`
  display: block;
  width: 100%;
  height: 3em;
  padding: 0;
  margin: 0;
  background-color: ${color.theme_color};
  font-size: ${textSize.s};
  text-align: center;
  line-height: 3em;
`

const Wrapper = styled.footer`
  display:block;
  width: 100%;
  padding-top:16px;
  margin: 0;
  background-color:${color.background_green};
`


export default GlobalFooter
